body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(1deg, #FF5EDF 0%, #04C8DE 100%);
  background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button{
  cursor: pointer;
}

.center{
  display: flex;
  justify-content: center;
}
header{
	height: 80px;
	top: 0;
    left: 0;
    right: 0;
    background: -webkit-linear-gradient(1deg, #FFEFFC 0%, #ECF9FB 100%);
    background: linear-gradient(89deg, #FFEFFC 0%, #ECF9FB 100%);
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.1rem;
/*    box-shadow: 0 0px 1px 0px #602E75;
*/}

header nav{
	margin-right: 20px;
}

header nav ul *{
    display: flex;
    justify-content: space-between;
}

header li {
    display: flex;
    margin: 20px;
}

header li p {
    color: #602E75;
    text-decoration: none;
    white-space: nowrap;
}

header h2{
    color: #602E75;
}

header img{
	margin-left: 40px;
	height: 70px;
}
.form{
	background:
		-webkit-radial-gradient(0% 50%,circle farthest-side,#fb1 23.5%,rgba(240,166,17,0) 0)21px 30px,
		-webkit-radial-gradient(0% 50%,circle farthest-side,#B71 24%,rgba(240,166,17,0) 0)19px 30px,
		-webkit-linear-gradient(#fb1 14%,rgba(240,166,17,0) 0, rgba(240,166,17,0) 85%,#fb1 0)0 0,
		-webkit-linear-gradient(300deg,#fb1 24%,#B71 0,#B71 26%,rgba(240,166,17,0) 0,rgba(240,166,17,0) 74%,#B71 0,#B71 76%,#fb1 0)0 0,
		-webkit-linear-gradient(60deg,#fb1 24%,#B71 0,#B71 26%,rgba(240,166,17,0) 0,rgba(240,166,17,0) 74%,#B71 0,#B71 76%,#fb1 0)0 0,
		-webkit-linear-gradient(left,#B71 2%,#fb1 0,#fb1 98%,#B71 0%)0 0 #fb1;
	background:
		radial-gradient(circle farthest-side at 0% 50%,#fb1 23.5%,rgba(240,166,17,0) 0)21px 30px,
		radial-gradient(circle farthest-side at 0% 50%,#B71 24%,rgba(240,166,17,0) 0)19px 30px,
		linear-gradient(#fb1 14%,rgba(240,166,17,0) 0, rgba(240,166,17,0) 85%,#fb1 0)0 0,
		linear-gradient(150deg,#fb1 24%,#B71 0,#B71 26%,rgba(240,166,17,0) 0,rgba(240,166,17,0) 74%,#B71 0,#B71 76%,#fb1 0)0 0,
		linear-gradient(30deg,#fb1 24%,#B71 0,#B71 26%,rgba(240,166,17,0) 0,rgba(240,166,17,0) 74%,#B71 0,#B71 76%,#fb1 0)0 0,
		linear-gradient(90deg,#B71 2%,#fb1 0,#fb1 98%,#B71 0%)0 0 #fb1;
	background-size: 40px 60px;
	max-width: 700px;
}

#formBtn{
	min-width: 70px;
}


.boundingBox{
	position: absolute;
	box-shadow: inset 0 0 0 3px #149df2;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.particles{
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
}

#mainDiv{
	margin-top: 80px;
	margin-bottom: 60px;
	margin-right: 1rem;
	margin-left: 1rem;
  position: relative;
}

.signIn{
	margin-top: 12rem;
}

@media screen and (max-width: 540px) {
    #logoName{
		display: none;
	}
	#formBtn, #formInput{
		font-size: 0.8rem;
	}
}

.spinner {
  width: 40px;
  height: 40px;

  margin: 100px auto;
  -webkit-animation: sk-rotateplane 1.2s infinite ease-in-out;
  animation: sk-rotateplane 1.2s infinite ease-in-out;
}

@-webkit-keyframes sk-rotateplane {
  0% { -webkit-transform: perspective(120px) }
  50% { -webkit-transform: perspective(120px) rotateY(180deg) }
  100% { -webkit-transform: perspective(120px) rotateY(180deg)  rotateX(180deg) }
}

@keyframes sk-rotateplane {
  0% { 
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    background-color: #ffebfc;
    -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg) 
  } 50% { 
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
    background-color: #c2ffe5;
    -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg) 
  } 100% { 
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    background-color: #ffebfc;
    -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}
